<template>
  <el-dialog title="同步菜单&权限" :visible.sync="dialogVisible" @close="dialogFormVisible" :close-on-click-modal="false" :close-on-press-escape="false">
    <el-form size="small" :model="formData" ref="form">
      <!--菜单名称-->
      <el-form-item label="请求网址" prop="url" :label-width="formLabelWidth">
        <el-input v-model="formData.url" autocomplete="off" placeholder="请输入请求网址"></el-input>
      </el-form-item>

      <!--选择同步类型-->
      <el-form-item label="是否是菜单" :label-width="formLabelWidth">
        <el-radio-group v-model="formData.type">
          <el-radio :label="1">全新</el-radio>
          <el-radio :label="0">增量</el-radio>
        </el-radio-group>
      </el-form-item>
      <el-form-item :label-width="formLabelWidth">
        <p>tips:选择<span style="color:#F56C6C">全新</span>则会删除本地的数据,建议先备份本地数据</p>
        <p>tips:选择<span style="color:#E6A23C">增量</span>则有可能无法完全同步到数据</p>
      </el-form-item>
      
    </el-form>  
    <div slot="footer" class="dialog-footer">
      <el-button @click="dialogFormVisible">取 消</el-button>
      <el-button type="primary" @click="onSubmit()" :disabled="loading">确 定</el-button>
    </div>
  </el-dialog>
</template>

<script>
import AccessApi from '@/api/access.js';
export default {
  data() {
    return {
      /*是否加载中*/
      loading: false,
      /*form表单数据对象*/
      formData: {
        /*请求网址*/
        url: 'http://h5dev.y01.cn/',
        /*类型*/
        type: 0
      },
      /*左边长度*/
      formLabelWidth: '120px',
      /*是否显示*/
      dialogVisible: false,
    };
  },
  props: {
    open_sync: Boolean,
    add_type: String,
    rawData: Array,
    selectModel: Object
  },
  created() {

    this.dialogVisible = this.open_sync;
  },
  methods: {
    /*选择菜单*/
    handleChange(e) {},

 

    /*同步菜单*/
    onSubmit() {
      let self = this;
      let params = this.formData;
      if(params.url==''){
        self.$message({
          message: "请求链接不可为空",
          type: 'error'
        });
        return false;
      }
      AccessApi.syncAccessList(params, true)
      .then(res => {
        self.loading = false;
        self.rawData = res.data;
        self.tableData = res.data;
        self.$message({
            type: 'success',
            message: res.msg
          });
      })
      .catch(error => {
        self.loading = false;
      }); 
    },

    /*关闭弹窗*/
    dialogFormVisible(e) {
      if (e) {
        this.$emit('closeDialog', {
          type: 'success',
          openDialog: false
        });
      } else {
        this.$emit('closeDialog', {
          type: 'error',
          openDialog: false
        });
      }
    }
  }
};
</script>

<style></style>



